:root {
    --text-color: #c5dce6;
    --background-light: #b6ccd1;
    --background-dark: #0c3345;
    --nav-sixe: 60px;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
    --speed: 500ms;
}
.App {
    display: flexbox;
}
/*/ Theme's for background, add .component to end for indiv use /*/
#light {
    background-color: var(--background-light);
}
#dark {
    background-color: var(--background-dark);
}

body {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: var(--text-color);
    text-decoration: none;
}